	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"When thinking about the future, you can't do better than Alan Kay. In The Future Doesn't Have To Be Incremental, Kay describes how Xerox PARC was able to develop so many new technologies in such a short time, including the personal computer, bitmap displays, GUI, desktop publishing, word processing, laser printing, Ethernet, and object oriented programming. The key was a culture focused on invention - that is, fundamentally new research - instead of incremental innovation. Invention requires a significantly higher investment of money, much longer time frames, and a different approach to problem solving.","meta":{"duration":"PT43M50S"},"presenter":{"name":"Alan Kay"},"source":{"id":"gTAghAJcO1o","provider":"youtube"},"slug":"the-future-doesnt-have-to-be-incremental","tag":["Inspiring","DEMO Conference 2014"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/gTAghAJcO1o/default.jpg","height":90,"width":120}},"title":"The Future Doesn't Have to Be Incremental"}}]}},"pathContext":{"slug":"demo-conference-2014","tag":"DEMO Conference 2014"}}

